/**
 * Represents an upgrade.
 */
export default class Upgrade
{
	/**
	 * The constructor.
	 *
	 * @param info the name, type, cost, etc. in an object
	 */
	constructor(info)
	{
		// populate this instance with all the info
		Object.assign(this, info);
		this.image = encodeURI(`images/upgrades/${this.name}.png`);
	}
};



