import React from "react";
import {formatDuration as _d, formatNumber as _, getAPS} from "../utils";

/**
 * Displays info such as adventures and adventures per second.
 */
export default class Info extends React.Component
{
	render()
	{
		let timeouts = this.props.data.upgrades.timeouts;
		return (
			<aside id="info">
				<p><strong>Adventures</strong>: {_(this.props.data.adventures)}</p>
				<p><strong>Adventures Per Second</strong>: {_(getAPS().total)}</p>
				<div id="timeouts">
					{Object.keys(timeouts).map(upgrade =>
						<span key={upgrade}>
							<strong>{upgrade}: </strong>{_d(timeouts[upgrade])}
						<br /></span>)}
				</div>
			</aside>
		);
	}
};



