import React from "react";
import ReactDOM from "react-dom";
import ReactTooltip from "react-tooltip";
import {DropTarget, DragSource} from "react-dnd";
import classNames from "classnames";
import DataStore from "../../data/DataStore";
import * as utils from "../../utils";
import {formatNumber as _} from "../../utils";

const buildingTargetSpec = {
	drop(props, mon)
	{
		props.onSetActive(props.index, mon.getItem().building);
	},
	canDrop(props, mon)
	{
		return props.type === mon.getItem().building.type;
	}
}

function collectTarget(connect, monitor)
{
	return {
		connectDropTarget: connect.dropTarget(),
		isOver: monitor.isOver()
	};
}

const buildingDragSpec = {
	beginDrag(props, mon, comp)
	{
		ReactTooltip.hide(comp.refs.img.parentNode);
		return {building: props.building};
	},
	canDrag(props)
	{
		return props.building.name !== "Empty";
	}
};

function collectDrag(connect, monitor)
{
	return {
		connectDragSource: connect.dragSource(),
		connectDragPreview: connect.dragPreview(),
		isDragging: monitor.isDragging()
	};
}

/**
 * Displays an active building.
 */
@DropTarget("building", buildingTargetSpec, collectTarget)
@DragSource("building", buildingDragSpec, collectDrag)
export default class ActiveBuilding extends React.Component
{
	static propTypes = {
		connectDragSource: React.PropTypes.func.isRequired,
		connectDropTarget: React.PropTypes.func.isRequired,
		connectDragPreview: React.PropTypes.func.isRequired,
		isDragging: React.PropTypes.bool.isRequired,
		isOver: React.PropTypes.bool.isRequired
	};

	componentDidMount()
	{
		this.img = new Image();
		this.img.src = this.props.building.image;
		this.img.onload = () => this.props.connectDragPreview(this.img);
	}

	componentWillReceiveProps(nextProps)
	{
		if (this.props.building.image !== nextProps.building.image)
			this.img.src = nextProps.building.image;
	}

	render()
	{
		const {connectDropTarget, connectDragSource, isOver, isDragging, building} = this.props;
		let data = DataStore.data;
		return connectDragSource(connectDropTarget(
			<div className={classNames({item: true, faded: isDragging || isOver})}>
				<div data-tip={`
					<div>
						<span>${building.name}</span><br />
						${building.name !== "Empty" ?
							`<span>APS: ${_(utils.getAPS().buildings[building.name] * data.buildings.owned[building.type][building.name])}` : ""}
					</div>
				`}>
					<img ref="img" draggable="false" src={building.image} />
				</div>
			</div>
		));
	}
};



