import React from "react";
import ListenerMethods from "reflux-core/lib/ListenerMethods";
import Type from "./Type";
import BuildingMenu from "./BuildingMenu";
import DataStore from "../../data/DataStore";
import * as actions from "../../actions";

/**
 * Displays the buildings and related menus and info.
 */
export default class Buildings extends React.Component
{
	state = {type: null};

	updateMenu(type)
	{
		this.setState({type: type});
	}

	render()
	{
		const {type} = this.state;
		let data = DataStore.data;
		let buildings = !type ? [] : this.props.buildings[type].map(building => {
			building.locked = false;
			building.faded = false;

			if (!Object.keys(this.props.owned[type]).includes(building.name))
			{
				if (data.adventures >= building.cost || data.stats.adventures.highest >= building.cost)
					building.faded = true;
				else building.locked = true;
			}

			return building;
		});
		return (
			<section id="items">
				<BuildingMenu
					ref="menu"
					onBuy={actions.buyBuilding}
					buildings={buildings} />

				{Object.keys(this.props.buildings).map(key => <Type
					key={key}
					onMouseEnter={this.updateMenu.bind(this, key)}
					onSetActive={actions.setActiveBuilding}
					active={this.props.active[key]}
					buildings={this.props.buildings[key]}
					owned={this.props.owned[key]}
					type={key} />)}
			</section>
		);
	}
};



