import * as actions from "../actions";
import DataStore from "../data/DataStore";

export default class StatsHandler
{
	static modifyStats(cb)
	{
		let data = DataStore.data;
		cb(data.stats, data);
		actions.setStats(data.stats);
	}

	static addPlaytime(playtime, offline)
	{
		this.modifyStats(stats =>
		{
			if (offline) stats.playtime.offline += playtime;
			else
			{
				stats.playtime.current += playtime;
				stats.playtime.total += playtime;

				let clazz = DataStore.data.class.name;
				if (clazz !== "None")
					stats.classes[clazz].timePlayed += playtime;
			}
		});
	}

	static addAdventures(adventures)
	{
		this.modifyStats((stats, data) => {
			if (data.adventures > stats.adventures.highest)
				stats.adventures.highest = data.adventures;

			stats.adventures.current += adventures;
			stats.adventures.total += adventures;
		});
	}

	static addAdventuresTicks(adventures)
	{
		this.modifyStats(stats => stats.adventures.ticks += adventures);
	}

	static addTicks(ticks)
	{
		this.modifyStats(stats => {
			stats.ticks.current += ticks;
			stats.ticks.total += ticks;
		});
	}
}



