export function getImage(name, gender, mood)
{
	return encodeURI(`images/classes/${name}/${gender}/${mood}.png`)
}

/**
 * Represents a class.
 */
export default class Class
{
	/**
	 * The constructor.
	 *
	 * @param info the name, perks, etc. in an object
	 */
	constructor(info)
	{
		// populate this instance with all the info
		Object.assign(this, info);
	}

	get image()
	{
		return getImage(this.name, this.gender, 1);
	}
}



