import React from "react";
import Modal from "react-modal";
import ReactTooltip from "react-tooltip";
import classNames from "classnames";
import * as actions from "../actions";
import classes from "../classes/classes";
import {getImage} from "../classes/Class";
import DataStore from "../data/DataStore";

export default class ClassSelection extends React.Component
{
	state = {gender: "m"};

	setClass(clazz)
	{
		clazz.gender = this.state.gender;
		actions.setClass(clazz);
	}

	componentWillReceiveProps()
	{
		ReactTooltip.rebuild();
	}

	render()
	{
		return (
			<Modal isOpen={this.props.open} shouldCloseOnOverlayClick={false} overlayClassName="class-overlay">
				<h2>Welcome to Adventurer!</h2>
				<h4>Pick a class.</h4>
				<div className="gender">
					<img onClick={() => this.setState({gender: "m"})}
						className={classNames({active: this.state.gender === "m"})} src="images/classes/male.png" />
					<img onClick={() => this.setState({gender: "f"})}
						className={classNames({active: this.state.gender === "f"})} src="images/classes/female.png" />
				</div>

				<div className="classes">
					{classes.map(c => (
						<div key={c.name} className="class">
							<p>{c.name}</p>
							<img src={getImage(c.name, this.state.gender, 1)} data-tip={c.description} />
							<button className="choose" onClick={this.setClass.bind(this, c)}>Choose</button>
						</div>
					))}
				</div>
			</Modal>
		);
	}
};



