import Reflux from "reflux-core";
import * as actions from "../actions";
import DataStore from "../data/DataStore";

export default Reflux.createStore({
	init()
	{
		this.listenToMany(actions);
	},

	hasRequired(upgrade, data)
	{
		if (!upgrade.requires) return true;
		for (let required of upgrade.requires)
			if (!data.upgrades.owned.map(u => u.name).includes(required)) return false;

		return true;
	},

	onBuyUpgrade(upgrade)
	{
		let data = DataStore.data;
		if (upgrade.locked && this.hasRequired(upgrade, data))
		{
			if (data.adventures >= upgrade.cost
				&& !Object.keys(data.upgrades.cooldowns).includes(upgrade.name))
			{
				if (upgrade.apply && upgrade.apply.timeout)
				{
					let timeouts = data.upgrades.timeouts;
					timeouts[upgrade.name] = upgrade.apply.timeout;
					actions.setUpgradeTimeouts(timeouts);
				}

				if (upgrade.apply && upgrade.apply.cooldown)
				{
					let cooldowns = data.upgrades.cooldowns;
					cooldowns[upgrade.name] = upgrade.apply.cooldown;
					actions.setUpgradeCooldowns(cooldowns);
				}

				actions.setAdventures(data.adventures - upgrade.cost);
				data.upgrades.owned = data.upgrades.owned.concat(upgrade);
				this.trigger(upgrade);
			}
			else return;
		}
	},

	onSetUpgradeTimeouts(timeouts)
	{
		let data = DataStore.data;
		data.upgrades.timeouts = timeouts;
		this.trigger();
	},

	onSetUpgradeCooldowns(cooldowns)
	{
		let data = DataStore.data;
		data.upgrades.cooldowns = cooldowns;
		this.trigger();
	}
});



