import React from "react";
import * as utils from "../../utils";
import {formatNumber as _} from "../../utils";
import DataStore from "../../data/DataStore";
import ActiveBuilding from "./ActiveBuilding";

/**
 * Displays a type of building and its currently active buildings.
 */
export default class Type extends React.Component
{
	render()
	{
		const {type, onMouseEnter, active, onSetActive} = this.props;
		let data = DataStore.data;
		return (
			<div id={this.props.type} className="type" onMouseEnter={onMouseEnter}>
				<div className="info">
					<span className="aps">{_(utils.getAPS().types[type])}</span>
				</div>
				<div className="active">
					{active.map((building, index) => <ActiveBuilding
						key={index}
						index={index}
						type={type}
						onSetActive={onSetActive}
						building={building} />)}
				</div>
			</div>
		);
	}
};



