import Reflux from "reflux-core";
import {cloneDeep} from "lodash";
import DataStore from "../data/DataStore";
import * as actions from "../actions";
import Building from "./Building";
import booze from "./booze";
import food from "./food";
import spleen from "./spleen";

export default Reflux.createStore({
	init()
	{
		this.types = {booze, food, spleen};
		this.origTypes = cloneDeep(this.types);

		this.listenToMany(actions);
	},

	/**
	 * Called when the data is finished loading.
	 */
	onLoadDataCompleted(data)
	{
		this.buildingCostIncrement = 15 + ((data.class.apply ? data.class.apply.buildingCostIncrement : 0) || 0);
		for (let type of Object.keys(this.types))
			// (percent / (100%) * value) * amt owned
			for (let building of this.types[type])
			{
				let owned = data.buildings.owned[building.type][building.name];
				building.origCost = building.cost;
				if (owned)
					building.cost += ((this.buildingCostIncrement / 100) * building.cost) * owned;
			}
	},

	onSetClass(clazz)
	{
		this.buildingCostIncrement = 15 + (clazz.apply.buildingCostIncrement || 0);
	},

	onBuyBuilding(amt, building)
	{
		let data = DataStore.data;
		let adv = data.adventures;
		if (adv >= building.cost)
		{
			let owned = data.buildings.owned[building.type];
			owned[building.name] = (owned[building.name] || 0) + amt;
			actions.setAdventures(adv - building.cost);
			building.cost += (this.buildingCostIncrement / 100) * building.cost;
			data.buildings.owned[building.type] = owned;
			this.trigger(building);
		}
	},

	onSetActiveBuilding(index, building)
	{
		let data = DataStore.data;
		// modify the active buildings and send action
		let active = data.buildings.active[building.type];
		if (active.includes(building))
			active[active.indexOf(building)] = active[index] || new Building({name: "Empty", adventuresPerSecond: 0});

		active[index] = building;
		data.buildings.active[building.type] = active;
		this.trigger(index, building);
	}
});



