/**
 * Represents a building.
 */
export default class Building
{
	/**
	 * The constructor.
	 *
	 * @param info the name, type, cost, etc. in an object
	 */
	constructor(info)
	{
		// populate this instance with all the info
		Object.assign(this, info);
		this.image = encodeURI(`images/items/${this.type}/${this.name}.png`);
	}
};



