import Reflux from "reflux-core";

/**
 * Starts loading the data from the database.
 */
export const loadData = Reflux.createAction({asyncResult: true});

/**
 * Saves the current data into the database.
 */
export const saveData = Reflux.createAction();

/**
 * Sets the amount of adventures in the data.
 *
 * @param payload the new amount of adventures
 */
export const setAdventures = Reflux.createAction();

/**
 * Sets the current charge in the data.
 *
 * @param current the new current charge amount
 */
export const setCurrentCharge = Reflux.createAction();

/**
 * Sets the max charge in the data.
 *
 * @param max the new max charge amount
 */
export const setMaxCharge = Reflux.createAction();

/**
 * Sets the options in the data.
 *
 * @param payload an object containing the options to be set, will be merged
 * with the existing options
 */
export const setOptions = Reflux.createAction();

/**
 * Set the current upgrade timeouts.
 *
 * @param timeouts the upgrade timeouts
 */
export const setUpgradeTimeouts = Reflux.createAction();

/**
 * Set the current upgrade cooldowns.
 *
 * @param cooldowns the upgrade cooldowns
 */
export const setUpgradeCooldowns = Reflux.createAction();

/**
 * Sets if the charge is currently being charged.
 *
 * @param charging if the charge is being charged
 */
export const setChargeCharging = Reflux.createAction();

/**
 * Sets the stats in the data.
 *
 * @param stats the new stats
 */
export const setStats = Reflux.createAction();

/**
 * Buys the specified building if possible.
 *
 * @param amt the amount of buildings to buy
 * @param building the building to buy
 */
export const buyBuilding = Reflux.createAction();

/**
 * Buys the specified upgrade if possible.
 *
 * @param building the upgrade to buy
 */
export const buyUpgrade = Reflux.createAction();

/**
 * Sets the active building for the buildings type to the building.
 *
 * @param index the index of the active building
 * @param building the new active building
 */
export const setActiveBuilding = Reflux.createAction();

/**
 * Sets the players class.
 *
 * @param class the class to set
 */
export const setClass = Reflux.createAction();



