import Reflux from "reflux-core";
import {isObject, isArray, isUndefined, isNull, cloneDeep} from "lodash";
import * as actions from "../actions";
import * as utils from "../utils";
import db from "./db";
import constants from "../constants";
import classes from "../classes/classes";
import BuildingStore from "../buildings/BuildingStore";
import UpgradeStore from "../upgrades/UpgradeStore";

export default Reflux.createStore({
	init()
	{
		this.data = {};

		this.listenToMany(actions);
		this.listenTo(BuildingStore, () => this.trigger());
		this.listenTo(UpgradeStore, () => this.trigger());
	},

	fillWithDefaults(data)
	{
		// recurses through a target object and sets defaults
		function recurse(defaults, target)
		{
			// go through all the defaults
			for (let key in defaults)
			{
				// make sure field isn't inherited
				if (defaults.hasOwnProperty(key))
				{
					// if the field is an object, recurse further
					if (!isArray(defaults[key]) && isObject(defaults[key]))
					{
						// if the target doesn't have the field, set it to an empty object so our function can work
						if (isUndefined(target[key]) || isNull(target[key])) target[key] = {};
						// recurse further
						recurse(defaults[key], target[key]);
						// continue so it doesn't run the other stuff
						continue;
					}

					// if the target doesn't have the field, set it from the defaults
					if (isUndefined(target[key])) target[key] = defaults[key];
				}
			}
		}

		// start the recursion on the top-level default and data objects
		recurse(constants.defaultData, data);
	},

	serialize(data)
	{
		for (let activeType of Object.keys(data.buildings.active))
			data.buildings.active[activeType] = utils.buildingNamesToObjects(data.buildings.active[activeType], true);

		data.upgrades.owned = utils.upgradeNamesToObjects(data.upgrades.owned, true);
		let clazz = {};
		clazz.class = utils.classNameToObject(data.class, true);
		clazz.gender = data.class.gender;
		data.class = clazz;
		data.charge.charging = false;

		return data;
	},

	deserialize(data)
	{
		// active buildings
		for (let activeType of Object.keys(data.buildings.active))
			data.buildings.active[activeType] = utils.buildingNamesToObjects(data.buildings.active[activeType]);

		// upgrades
		data.upgrades.owned = utils.upgradeNamesToObjects(data.upgrades.owned);

		// class
		let clazz = {};
		clazz = utils.classNameToObject(data.class.class);
		clazz.gender = data.class.gender;
		data.class = clazz;

		// stats
		for (let name of classes.map(c => c.name))
			data.stats.classes[name] = data.stats.classes[name] || {timePlayed: 0, ascensions: 0};

		return data;
	},

	async onLoadData()
	{
		this.trigger();
		try
		{
			// load our data from db, default to empty object if undefined
			let data = await db.get();
			data = data || {};
			this.fillWithDefaults(data);

			this.data = this.deserialize(data);
			actions.loadData.completed(this.data);
			// trigger a save with our new data
			actions.saveData(this.data)
			this.trigger();
		}
		catch (err)
		{
			// there was an error
			actions.loadData.failed(err);
			this.trigger();
		}
	},

	async onSaveData()
	{
		// set last save time
		this.data.lastSave = Date.now();
		await db.save(this.serialize(cloneDeep(this.data)));
		this.trigger();
	},

	onSetAdventures(adventures)
	{
		this.data.adventures = adventures;
		this.trigger();
	},

	onSetStats(stats)
	{
		this.data.stats = stats;
		this.trigger();
	},

	onSetCurrentCharge(current)
	{
		this.data.charge.current = current;
		this.trigger();
	},

	onSetMaxCharge(max)
	{
		this.data.charge.max = max;
 		this.trigger();
	},

	onSetOptions(options)
	{
		this.data.options = options;
		this.trigger();
	},

	onSetChargeCharging(charging)
	{
		this.data.charge.charging = charging;
		this.trigger();
	},

	onSetClass(clazz)
	{
		this.data.class = clazz;
		this.trigger();
	}
});



