export default class Timer
{
	/**
	* The handler that runs the timer at the specified interval.
	*
	* @private
	*/
	handler = () =>
	{
		this.callback();

		this.id = setTimeout(this.handler, this.interval);
	};

	/**
	 * Creates a new timer.
	 *
	 * @param interval the interval to call the function at, in milliseconds
	 * @param func the function to be called each time the interval passes
	 */
	constructor(interval, func)
	{
		this.interval = interval;
		this.callback = func;
	}

	/**
	 * Starts the timer.
	 * If the timer is already running, nothing will happen.
	 */
	start()
	{
		if (this.id) return;
		this.id = setTimeout(this.handler, this.interval);
	}

	/**
	 * Stops the timer.
	 * If the timer is not running, nothing will happen.
	 */
	stop()
	{
		if (!this.id) return;
		clearTimeout(this.id);
		this.id = null;
	}
};



