import {isNumber, isString} from "lodash"
import Building from "./buildings/Building";
import BuildingStore from "./buildings/BuildingStore";
import DataStore from "./data/DataStore";
import classes from "./classes/classes";
import Class from "./classes/Class";
import normal from "./upgrades/normal";
import clan from "./upgrades/clan";

const buildings = (() => {
	let arr = [];
	for (let type of Object.keys(BuildingStore.types))
		for (let building of BuildingStore.types[type]) arr.push(building);

	return arr;
})();
const upgrades = normal.concat(clan);
// amt of commas -> name
const formatNames = {
	2: "Million",
	3: "Billion",
	4: "Trillion",
	5: "Quadrillion",
	6: "Quintillion",
	7: "Sextillion",
	8: "Septillion",
	9: "Octillion",
	10: "Nonillion",
	11: "Decillion",
	12: "Undecillion",
	13: "Tredecillion",
	14: "Quattuordecillion",
	15: "Quindecillion",
	16: "Sexdecillion",
	17: "Septendecillion",
	18: "Octodecillion",
	19: "Novemdecillion",
	20: "Vigintillion"
};
let cachedAPS = {};

// gets the first number in a string and returns it
function getNumber(str)
{
	return parseInt(str.match(/\d+/));
}

/**
* Converts a number like "1000000" into "1 Million",
* and a number like "100000" into "100,000".
*
* @param num {number} a number to format
* @returns {string} the formatted number
*/
export function formatNumber(num)
{
	let fmted = Math.ceil(num).toLocaleString();
	// get amount of commas in formatted number
	let commas = fmted.split(/\D/).length - 1;
	// if we have a name for the number (checking amount of commas)
	if (formatNames[commas])
	{
		let divisor = "1";
		// for each comma, add a 000
		for (let i = 1; i <= commas; i++) divisor += "000";

		divisor = parseInt(divisor, 10);
		fmted = `${(num / divisor).toFixed(2).toLocaleString()} ${formatNames[commas]}`;
	}

	return fmted;
};

/**
 * Formats a duration in ms to a string like "ddhhmmss"
 *
 * @param ms {number} the duration in milliseconds
 * @return {string} the formatted string
 */
export function formatDuration(ms)
{
	let str = "";
	let seconds = Math.trunc(ms / 1000);

	let days = Math.trunc(seconds / 86400);
	if (days >= 1)
	{
		str += days + "d";
		seconds -= 86400 * days;
	}

	let hours = Math.trunc(seconds / 3600);
	if (hours >= 1)
	{
		str += hours + "h";
		seconds -= 3600 * hours;
	}

	let minutes = Math.trunc(seconds / 60);
	if (minutes >= 1)
	{
		str += minutes + "m";
		seconds -= 60 * minutes;
	}

	if (seconds >= 1 || str === "")
	{
		str += seconds + "s";
	}

	// add space if not last param
	return str.replace(/(d|h|m|s)(?!$)/g, "$1 ");
}

/**
 * Checks if the input is a percentage or number, and returns an object
 * accordingly.
 *
 * @param input {number | string} a number or a string starting with a number
 * and ending with a "%"
 * @return {object} an object with the properties "percentage" and "number",
 * where "percentage" is true if the input was a percentage, and "number" is the
 * percentage or input number.
 */
export function numberOrPercentage(input)
{
	if (isNumber(input)) return {number: input, percentage: false};
	if (isString(input)) return {number: getNumber(input), percentage: true};
};

/**
 * Turns an array of building names into an array of their respective building
 * objects.
 *
 * @param buildingNames an array of building names
 * @param serialize reverse for serialization
 * @returns {array} the array of building objects
 */
export function buildingNamesToObjects(buildingNames, serialize)
{
	// map the active building names to the building object
	return buildingNames.map(buildingName =>
	{
		// if this building is null, set it to empty building
		if (!buildingName)
			return new Building({name: "Empty", adventuresPerSecond: 0});

		return serialize ? (buildingName.name === "Empty" ? null : buildingName.name)
		: buildings.find(function(building)
		{
			if (building.name === buildingName) return building;
			return false;
		});
	});
};

/**
 * Turns an array of upgrade names into an array of their respective upgrade
 * objects.
 *
 * @param upgradeNames an array of upgrade names
 * @param serialize reverse for serialization
 * @returns {array} the array of upgrade objects
 */
export function upgradeNamesToObjects(upgradeNames, serialize)
{
	return upgradeNames.map(upgradeName =>
	{
		return serialize ? upgradeName.name : upgrades.find(function(upgrade)
		{
			if (upgrade.name === upgradeName) return upgrade;
			return false;
		});
	});
};

/**
 * Turns a class name into its respective class object.
 *
 * @param className the class name
 * @param serialize reverse for serialization
 * @returns {Class} the class object
 */
export function classNameToObject(className, serialize)
{
	if (className === null) return new Class({name: "None"});

	return serialize ? (className.name === "None" ? null : className.name)
	: classes.find(function(clazz)
	{
		if (clazz.name === className) return clazz;
		return false;
	});
};

export function calculateAPS()
{
	let data = DataStore.data;
	let active = (() => {
		let arr = [];
		for (let activeType of Object.keys(data.buildings.active))
			for (let building of data.buildings.active[activeType])
				if (building.adventuresPerSecond !== 0) arr.push(building);

		return arr;
	})();
	let aps = {types: {booze: 0, food: 0, spleen: 0}, buildings: {}};
	// calculate aps of all buildings
	for (let building of buildings)
	{
		aps.buildings[building.name] = building.adventuresPerSecond;
	}

	for (let upgrade of data.upgrades.owned.concat([data.class]))
	{
		// skip if upgrade doesnt have aps addition
		if (!upgrade.apply || !upgrade.apply.adventuresPerSecond) continue;

		let applyFor = upgrade.apply.for || ["booze", "food", "spleen"];
		let ret = numberOrPercentage(upgrade.apply.adventuresPerSecond);
		for (let building of buildings)
		{
			if (!applyFor.includes(building.type)) continue;

			let bAps = aps.buildings[building.name];
			if (ret.percentage)
				// percent / (100%) * aps
				aps.buildings[building.name] += (ret.number / 100) * bAps;
			else
				aps.buildings[building.name] += ret.number;
		}
	}

	for (let building of active) aps.types[building.type] += aps.buildings[building.name]
		* (data.buildings.owned[building.type][building.name] || 1);

	let total = 0;
	for (let type of Object.keys(aps.types)) total += aps.types[type];
	aps.total = total;

	cachedAPS = aps;
};

/**
 * Returns the cached APS of all active buildings.
 *
 * @returns {Object} an object with the aps
 */
export function getAPS()
{
	return cachedAPS;
};

// called when the app is loaded, shows app and hides loading screen
export function loaded()
{
	document.querySelector("#loading").style.display = "none";
	document.querySelector("#app").style.display = "block";
};



